import React from 'react';
import './Navigation.css';

class Navigation extends React.Component {
	constructor(props) {
		super(props);
		this.toggleClass= this.toggleClass.bind(this);
    this.state = { active: 0 };
	}
	toggleClass(index) {
    if (this.state.active === index) {
      this.setState({ active: null })
    } else {
      this.setState({ active: index })
    }
  }
  activeHandle(index) {
    if (this.state.active === index) {
      return "active-nav-item";
    }
    return "";
  }

	render() {
		return(	
			<nav id="navigation">
				<ul ref={this.toTop}>
					{/*<li className={this.activeHandle(0)} onClick={() => this.toggleClass(0)} ><span>My Portfolios</span></li>*/}
					{/*<li onClick={this.scrollToTop}><span>Top</span></li>*/}
					<li><a href="#app">Top</a></li>
					<li><a href="#portfolios">My Projects</a></li>
					<li><a href="#skills">My Skills</a></li>
					<li><a href="#about">About Me</a></li>
				</ul>
			</nav>
		)
	}
} 

export default Navigation;