import React from 'react';
import './About.css';

class About extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			page: 'about'
		}
	}
	render() {
		return (
			<section id="about" className="parallax">
			  <section className="about-content">
				  <h1 className="f2 f1-l ttu near-white t-shadow-black">About Me <span className="f6 f4-l t-shadow-black">Here's why I build.</span></h1>
				  <article className="about pv4 ph4 br3 shadow-2 mb2">
		        <div className="f5 f4-l ma0 pa0">
		        	<div className="db br2 f4 f3-l b mb0 pa0 lightest-blue">Technology</div>
		          <p className="mt2 mb4 lh-copy f6 f5-l">
		          	The improvements in technology over the last 15 years have been huge. We interact with technology on a daily basis and the tech industry now has one of the most significant impacts on the world. The industry is constantly changing and <span className="bg-green br2 b ph2">I believe that change is good, and that learning should never stop.</span> I love learning new things on a daily basis as a Developer and being at the forefront of this phenomenal industry. Over the last several years things have begun moving incredibly fast and web development is becoming even more important as Web Apps replace traditional software.
		          </p>
		        	<div className="db br2 f4 f3-l b mb0 pa0 lightest-blue">Think Different</div>
		          <p className="mt2 mb4 lh-copy f6 f5-l">
		          	I love a good challenge and trying new things to solve them. I really enjoy the creative process of envisioning solutions to problems and developing products. I now develop technology that <span className="bg-green br2 b ph2">improves the way we learn, complete everyday tasks and the way we do our work.</span> I gained invaluable experience from previous jobs where I taught people how to use technology to improve their lives or workflow, and from my own experiences as a user using a large multitude of different technologies.
		          </p>
		        	<div className="db br2 f4 f3-l b mb0 pa0 lightest-blue">Little Firstly</div>
		          <p className="mt2 mb2 lh-copy f6 f5-l">
		          	Everythings is start from little thing. Then the bunch of little things are going to do The Great Thing. I know that, <span className="bg-green br2 b ph2"> little things make all the difference.</span>
		          </p>
		        </div>
				  </article>
				  <article className="about pv4 ph4 br3 shadow-2 mb0">
		        <div className="f5 f4-l ma0 pa0">
		        	<div className="db br2 f4 f3-l b mb0 pa0 light-yellow tc">Contact Me</div>
		          <p className="mt2 mb2 lh-copy f6 f5-l tc">
		          	Email: k.sammanak@gmail.com<br/>
		          	Phone: (+855) 96-788-8005
		          </p>
		        </div>
				  </article>			
				</section>
			</section>
		)
	}
}

export default About;